import React, { Component } from 'react';
import axios from 'axios';
import { MDBDataTable, MDBContainer, MDBRow, MDBCol } from 'mdbreact';
import ToggleFavourite from './add-fav';
import {
  CPagination,
  CSelect,
  CRow,
  CCol,
  CCard,
  CCardHeader,
  CCardBody,
} from '@coreui/react';

const columns = [
  {
    label: 'Id',
    field: 'id',
    sort: 'asc',
  },
  {
    label: 'Domain',
    field: 'domain',
    sort: 'asc',
  },
  {
    label: 'Metrics',
    field: 'metrics',
    sort: 'asc',
  },
  {
    label: 'Words',
    field: 'words',
    sort: 'asc',
  },
  {
    label: 'Sentiment_score',
    field: 'sentiment_score',
    sort: 'asc',
  },
  {
    label: 'Add_Fav',
    field: 'add_fav',
    sort: 'asc',
  },
];

export default class CustomTables extends Component {
  state = {
    togglesearch: false,
    pageNo: 1,
    size: 5,
    pagesAmount: 1,
    keyword: null,
    columns: [...columns],
    rows: [],
    domain: null,
    length: null,
    alphanumeric: null,
    numeric: null,
    hyphenated: null,
    tld: null,
    wordcount: null,
    words: null,
    score: null,
  };
  toggleSearch = () => {
    this.setState({
      pageNo: 1,
      size: 5,
      pagesAmount: 1,
      keyword: null,
      domain: null,
      metrics: null,
      words: null,
      score: null,
      togglesearch: !this.state.togglesearch,
    });
  };
  onPageChange = (e) => {
    this.setState({ pageNo: e }, () => {
      this.getData(this.state);
    });
  };
  onChange = (e) => {
    const { name, value } = e.target;
    this.setState(
      {
        [name]: value.trim(),
      },
      () => {
        this.getData(this.state);
      }
    );
  };
  getData = async (args) => {
    await axios
      .post('api/get_domains', {
        ...args,
      })
      .then((res) => {
        console.log(res);
        let data = res.data.data;
        let pages = res.data.pages;
        let rows = [];
        if (res.data.data.length > 0) {
          data.map((data) => {
            rows.push({
              id: data._id,
              domain: data.domain,
              metrics: (
                <>
                  <span>alphanumeric: {data.metrics.alphanumeric}</span>
                  <hr className='p-0 m-0' />
                  <span>hyphenated :{data.metrics.hyphenated}</span>
                  <hr className='p-0 m-0' />
                  <span>
                    length:
                    {data.metrics.length}
                  </span>
                  <hr className='p-0 m-0' />
                  ​​ <span>numeric: {data.metrics.numeric}</span>
                  <hr className='p-0 m-0' />
                  <span>
                    tld:
                    {data.metrics.tld}
                  </span>
                  <hr className='p-0 m-0' />
                  ​​ <span>wordcount{data.metrics.wordcount}</span>
                  <hr className='p-0 m-0' />
                </>
              ),
              words: (
                <>
                  {data.words.map((w, key) => {
                    return (
                      <span key={key}>
                        {w} <hr className='p-0 m-0' />
                      </span>
                    );
                  })}
                </>
              ),
              sentiment_score: data.sentiment_score,
              add_fav: (
                <button
                  className={
                    !data.is_favourite
                      ? 'btn btn-success btn-sm'
                      : 'btn btn-danger btn-sm'
                  }
                  onClick={() => {
                    ToggleFavourite(data._id, !data.is_favourite);
                    this.getData(this.state);
                  }}>
                  {!data.is_favourite ? <> add fav</> : <>remove fav</>}
                </button>
              ),
            });
          });
          console.log('Setting state . .. ', rows);
          this.setState({
            ...this.state,
            rows,
            pagesAmount: pages,
          });
        } else {
          this.setState({
            ...this.state,
            rows: [],
            pageNo: 1,
            pagesAmount: 1,
          });
        }
      })
      .catch((err) => {
        console.log(err);
        this.setState({ ...this.state, rows: [], pageNo: 1, pagesAmount: 1 });
      });
  };
  render() {
    return (
      <>
        <header className='text-align-center'>
          <h1>SEARCH COMPONENT</h1>
        </header>
        <CRow className='m-0 p-0'>
          <CCol xs='3' sm='2' md='1' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                <button
                  style={{ height: '35px' }}
                  className='btn btn-sm btn-success border-outline-green btn-block'
                  onClick={this.toggleSearch}>
                  Toggle
                </button>
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs='6' sm='8' md='9' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                <input
                  style={{ height: '35px' }}
                  placeholder='SuperSearch'
                  disabled={this.state.togglesearch}
                  name='keyword'
                  onChange={this.onChange}
                  type='text'
                  className='form-control form-control-sm m-0 p-0'
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs='2' sm='1' md='1' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                <CSelect
                  custom
                  name='size'
                  placeholder='No of Entries'
                  onChange={this.onChange}
                  id='ccmonth'>
                  <option value='5'>5</option>
                  <option value='10'>10</option>
                  <option value='20'>20</option>
                  <option value='30'>30</option>
                  <option value='40'>40</option>
                  <option value='50'>50</option>
                  <option value='60'>60</option>
                  <option value='70'>70</option>
                  <option value='80'>80</option>
                  <option value='90'>90</option>
                  <option value='100'>100</option>
                  <option value='500'>500</option>
                  <option value='1000'>1000</option>
                </CSelect>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
        <CRow className='m-0 p-0'>
          <CCol xs='5' sm='4' md='2' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                <input
                  style={{ height: '35px' }}
                  placeholder='Domain'
                  disabled={!this.state.togglesearch}
                  name='domain'
                  onChange={this.onChange}
                  type='text'
                  className='form-control form-control-sm'
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs='5' sm='4' md='2' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                <input
                  style={{ height: '35px' }}
                  placeholder='m.length'
                  disabled={!this.state.togglesearch}
                  name='length'
                  onChange={this.onChange}
                  type='number'
                  className='form-control form-control-sm'
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs='5' sm='4' md='2' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                {' '}
                <input
                  style={{ height: '35px' }}
                  placeholder='m.alphanumeric'
                  disabled={!this.state.togglesearch}
                  name='alphanumeric'
                  onChange={this.onChange}
                  type='number'
                  className='form-control form-control-sm'
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs='5' sm='4' md='2' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                {' '}
                <input
                  style={{ height: '35px' }}
                  placeholder='m.hyphenated'
                  disabled={!this.state.togglesearch}
                  name='hyphenated'
                  onChange={this.onChange}
                  type='number'
                  className='form-control form-control-sm'
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs='5' sm='4' md='2' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                {' '}
                <input
                  style={{ height: '35px' }}
                  placeholder='m.tld'
                  disabled={!this.state.togglesearch}
                  name='tld'
                  onChange={this.onChange}
                  type='text'
                  className='form-control form-control-sm'
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs='5' sm='4' md='2' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                {' '}
                <input
                  style={{ height: '35px' }}
                  placeholder='m.wordcount'
                  disabled={!this.state.togglesearch}
                  name='wordcount'
                  onChange={this.onChange}
                  type='number'
                  className='form-control form-control-sm'
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs='5' sm='4' md='2' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                <input
                  style={{ height: '35px' }}
                  placeholder='Words'
                  disabled={!this.state.togglesearch}
                  name='words'
                  onChange={this.onChange}
                  type='text'
                  className='form-control form-control-sm'
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs='5' sm='4' md='2' className='m-1 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='m-0 p-0'>
                <input
                  style={{ height: '35px' }}
                  placeholder='Sentimental score'
                  disabled={!this.state.togglesearch}
                  name='score'
                  onChange={this.onChange}
                  type='number'
                  className='form-control form-control-sm'
                />
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>

        <CRow>
          <CCol className='m-0 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='heavy-rain-gradient m-0 p-0'>
                <MDBDataTable
                  className='text-dark black-text p-2 strong'
                  searching={false}
                  responsive
                  bordered={true}
                  paging={false}
                  striped
                  theadColor='dark'
                  small
                  noBottomColumns
                  hover
                  data={this.state}
                />
                <CPagination
                  align='center'
                  activePage={this.state.pageNo}
                  pages={this.state.pagesAmount}
                  onActivePageChange={this.onPageChange}
                />
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}
